import React, { useState, useEffect } from "react";
import { Card, CardContent } from "../ui/card";
import { Slider } from "../ui/slider";
import { motion } from "framer-motion";

export default function SavingsCalculator() {
  const [investment, setInvestment] = useState(10000);
  const [period, setPeriod] = useState(5);
  const [returns, setReturns] = useState({
    futureValue: 0,
    interestEarned: 0,
  });

  const interestRate = 0.225; // Updated to 22.5%

  useEffect(() => {
    // FV = P * (1 + r/n)^(n*t)
    const P = investment;
    const r = interestRate;
    const n = 365; // Compounded daily
    const t = period;

    const futureValue = P * Math.pow(1 + r / n, n * t);
    const interestEarned = futureValue - P;

    setReturns({ futureValue, interestEarned });
  }, [investment, period]);

  return (
    <section className="py-20 bg-white">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-slate-900">Savings Calculator</h2>
          <p className="text-slate-600 mt-2 max-w-2xl mx-auto">
            See how much your savings could grow over time with our competitive yield rates.
          </p>
        </div>

        <div className="grid lg:grid-cols-2 gap-8 items-center max-w-4xl mx-auto">
          <Card className="p-8 border-none shadow-2xl shadow-blue-500/10">
            <CardContent className="p-0 space-y-8">
              <div>
                <div className="flex justify-between items-center mb-2">
                  <label className="font-medium text-slate-700">Initial Investment</label>
                  <span className="font-bold text-blue-600 text-lg">
                    ${investment.toLocaleString()}
                  </span>
                </div>
                <Slider
                  defaultValue={[10000]}
                  min={1000}
                  max={100000}
                  step={500}
                  onValueChange={(value) => setInvestment(value[0])}
                />
              </div>

              <div>
                <div className="flex justify-between items-center mb-2">
                  <label className="font-medium text-slate-700">Time Period</label>
                  <span className="font-bold text-blue-600 text-lg">
                    {period} {period > 1 ? "years" : "year"}
                  </span>
                </div>
                <Slider
                  defaultValue={[5]}
                  min={1}
                  max={30}
                  step={1}
                  onValueChange={(value) => setPeriod(value[0])}
                />
              </div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-blue-600 to-indigo-700 text-white p-8 shadow-2xl shadow-indigo-500/40">
            <CardContent className="p-0 text-center">
              <h3 className="text-xl font-semibold mb-4 text-blue-100">Your Projected Returns</h3>
              <div className="space-y-6">
                <div>
                  <p className="text-4xl font-bold">${returns.futureValue.toLocaleString('en-US', { minimumFractionDigits: 0, maximumFractionDigits: 0 })}</p>
                  <p className="text-sm text-blue-200">Future Value</p>
                </div>
                <div>
                  <p className="text-2xl font-bold text-blue-100">${returns.interestEarned.toLocaleString('en-US', { minimumFractionDigits: 0, maximumFractionDigits: 0 })}</p>
                  <p className="text-sm text-blue-200">Total Interest Earned</p>
                </div>
                <div>
                  <p className="text-lg font-semibold bg-blue-500/50 rounded-full px-4 py-1 inline-block">{(interestRate * 100).toFixed(1)}% Annual Interest Rate</p>
                </div>
              </div>
              <p className="text-xs text-blue-300 mt-6">Interest is compounded daily and paid out to your account automatically.</p>
            </CardContent>
          </Card>
        </div>
      </div>
    </section>
  );
}
