import React, { useState } from 'react';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { ArrowRight, CheckCircle, AlertCircle, Loader2 } from 'lucide-react';
import Airtable from 'airtable';

const WhitelistForm = ({ 
  buttonText = "Get on Whitelist", 
  buttonClassName = "bg-blue-600 hover:bg-blue-700 text-white px-8 py-3 shadow-lg text-base",
  size = "lg",
  variant = "default",
  showArrow = true 
}) => {
  const [email, setEmail] = useState('');
  const [status, setStatus] = useState('idle'); // idle, loading, success, error
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!email || !email.includes('@')) {
      setStatus('error');
      setMessage('Please enter a valid email address');
      return;
    }

    setStatus('loading');
    setMessage('');

    try {
      const base = new Airtable({
        apiKey: 'patVVTo0v3cbtB1kg.1a9bb107b20941a593dca381b9a6eec42f92502f7a5572e6a091090766455bf6'
      }).base('app9FxiRPJa9xKidP');
      
      // Add email to Airtable using the correct table name and fields
      base('Emails').create([
        {
          fields: {
            'Email Address': email,
          }
        }
      ], function(err, records) {
        if (err) {
          console.error('Error adding to whitelist:', err);
          setStatus('error');
          setMessage('Something went wrong. Please try again.');
          return;
        }
        
        setStatus('success');
        setMessage('Successfully added to whitelist!');
        setEmail('');
        console.log('Record created with ID:', records[0].getId());
      });
    } catch (error) {
      console.error('Error adding to whitelist:', error);
      setStatus('error');
      setMessage('Something went wrong. Please try again.');
    }
  };

  if (status === 'success') {
    return (
      <div className="flex items-center gap-2 text-green-600">
        <CheckCircle className="w-5 h-5" />
        <span className="font-medium">{message}</span>
      </div>
    );
  }

  return (
    <form onSubmit={handleSubmit} className="w-full max-w-md">
      <div className="flex flex-col sm:flex-row bg-white rounded-xl shadow-lg border border-slate-200 overflow-hidden transition-all duration-200 hover:shadow-xl focus-within:ring-2 focus-within:ring-blue-200 focus-within:border-blue-400">
        <div className="flex-1">
          <Input
            type="email"
            placeholder="Enter your email address"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="w-full h-14 px-4 text-base bg-transparent border-0 focus:ring-0 focus:outline-none placeholder:text-slate-400 rounded-none"
            disabled={status === 'loading'}
          />
        </div>
        <Button 
          type="submit" 
          size={size}
          variant={variant}
          className={`${buttonClassName} h-14 px-6 rounded-none sm:rounded-r-xl border-0 shadow-none font-semibold whitespace-nowrap`}
          disabled={status === 'loading'}
        >
          {status === 'loading' ? (
            <>
              <Loader2 className="w-4 h-4 mr-2 animate-spin" />
              Adding...
            </>
          ) : (
            <>
              {buttonText}
              {showArrow && <ArrowRight className="w-5 h-5 ml-2" />}
            </>
          )}
        </Button>
      </div>
      {status === 'error' && (
        <div className="flex items-center gap-1 mt-2 text-red-600 text-sm">
          <AlertCircle className="w-4 h-4" />
          <span>{message}</span>
        </div>
      )}
    </form>
  );
};

export default WhitelistForm;
